.roundsComponent {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  max-width: 100%;
  overflow: auto;
  background-color: var(--color-white);

  display: flex;
  flex-direction: column;
  justify-content: center;
  row-gap: 8px;
  overflow: hidden;
}